*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --col-bg-primary: #fdf0e9;
  --col-bg-light: #fff;
  --col-bg-middle: #ef6d58;
  --col-bg-dark: #28293e;
  --col-text-primary: #391400;
  --col-text-secondary: #fff;
  --color-text-inactive-dark: rgba(57, 20, 0, .64);
  --color-text-inactive-light: rgba(255, 255, 255, .64);
  --col-title-primary: #391400;
  --col-title-secondary: #fff;
  --col-accent-primary: #ef6d58;
  --col-accent-secondary: #b550f8;
  --col-btn-default: #fff;
  --col-btn-hover-dark: #391400;
  --col-btn-hover-light: #ef6d58;
  --col-btn-focus: #ef6d58;
  --col-btn-disabled: #000;
  --col-btn-bg-default-primary: #ef6d58;
  --col-btn-bg-default-secondary: #b550f8;
  --col-btn-bg-hover: #fff;
  --col-btn-bg-focus: rgba(255, 255, 255, .3);
  --col-btn-bg-disabled: #ababab;
  --border-btn-focus-light: 2px solid rgba(255, 255, 255, .3);
  --fill-icon: #fdf0e9;
  --col-bg-icon: #ef6d58;
  --col-bg-icon-hover: rgba(255, 255, 255, .3);
  --col-bg-icon-focus: transparent;
  --col-backdrop: rgba(0, 0, 0, .2);
  --border: 1px solid #f3d1bf;
  --cubic-function: cubic-bezier(.4, 0, .2, 1);
  --animation-time: 250ms;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
